.submit-a-quote {
  font-size: 16px;
  form {
    display: flex;
    flex-direction: column;
  }

  label {
    margin-bottom: 10px;
  }
  input {
    margin-bottom: 20px;
    background-color: $grey;
    border: 2px solid $green;
    border-radius: 2px;
    height: 30px;
    color: white;
  }
}
#quote-form {
  height: 60px;
}

#submit {
  color: $green;
  width: 100px;
  margin-top: 25px;
  height: 30px;
  padding: 2px 5px;
  cursor: pointer;
  &:hover {
    color: #090;
  }
}
